@media (min-width: 768px) {
    //- Announcement banner
    .wfs--phone-call {
        margin-left: 1.5rem;
    }

    .slogan {
        margin-left: 15rem;
    }

    //- Nav menu
    .menu {
        margin-top: 0.65rem;
    }

    //- How we can help
    .wfs--pie-chart,
    .wfs--user,
    .wfs--git-branch,
    .wfs--database,
    .wfs--trending-up,
    .wfs--cloud {
        margin-top: 0.5rem;
    }

    //- Testimonials
    .small-heading {
        font-family: 'Ubuntu';
        font-size: 0.6rem;
        text-align: center;
        text-transform: capitalize;
        padding-top: 17rem;
    }

    .larger-heading {
        font-family: 'Ubuntu';
        font-size: 1rem;
        text-align: center;
        text-transform: capitalize;
        padding-top: 0.5rem;
    }

    .sushi-hr {
        background-color: $color-sushi;
        color: $color-sushi;
        height: 0.1rem;
        width: 1.5rem;
    }

    .start-quotation-marks {
        color: $color-indigo;
        margin-top: 0.5rem;
        margin-left: 7rem;
    }

    .end-quotation-marks {
        color: $color-indigo;
        margin-top: 0.5rem;
        margin-left: 30rem;
    }

    .quote {
        font-size: 0.55rem;
        font-style: italic;
        text-align: center;
    }

    .name {
        font-size: 0.5rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .job {
        color: $color-boulder;
        font-size: 0.5rem;
        font-style: italic;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}